import React, { createContext, useContext, useState, useEffect, useCallback } from 'react';
import { fetchPokemons, fetchPokemonById } from '../services/pokemonService';

// Criando o contexto
const PokemonContext = createContext();

// Hook personalizado para usar o contexto
export const usePokemon = () => useContext(PokemonContext);

// Provedor do contexto
export const PokemonProvider = ({ children }) => {
  const [pokemons, setPokemons] = useState([]);
  const [filteredPokemons, setFilteredPokemons] = useState([]);
  const [loading, setLoading] = useState(true);
  const [loadingMore, setLoadingMore] = useState(false);
  const [error, setError] = useState(null);
  const [hasMore, setHasMore] = useState(true);
  const [page, setPage] = useState(1);
  const [searchTerm, setSearchTerm] = useState('');
  const [isSearching, setIsSearching] = useState(false);
  const [filters, setFilters] = useState({
    types: [],
    weaknesses: [],
    abilities: [],
    heightRange: [0, 20],
    weightRange: [0, 1000],
    sortOrder: 'asc',
    minNumber: 1,
    maxNumber: 1010
  });

  // Função para carregar os Pokémon iniciais
  const loadInitialPokemons = useCallback(async () => {
    try {
      setLoading(true);
      setError(null);
      const data = await fetchPokemons(1);
      setPokemons(data);
      setFilteredPokemons(data);
      setPage(1);
      setHasMore(data.length === 20);
    } catch (err) {
      console.error('Erro ao carregar Pokémon:', err);
      setError('Falha ao carregar os Pokémon. Por favor, tente novamente mais tarde.');
    } finally {
      setLoading(false);
    }
  }, []);

  // Carregar mais Pokémon
  const loadMorePokemons = useCallback(async () => {
    if (loadingMore || !hasMore || isSearching) return;

    try {
      setLoadingMore(true);
      const nextPage = page + 1;
      const newPokemons = await fetchPokemons(nextPage);
      
      if (newPokemons.length === 0) {
        setHasMore(false);
        return;
      }
      
      setPokemons(prevPokemons => {
        const updatedPokemons = [...prevPokemons, ...newPokemons];
        return updatedPokemons;
      });
      
      setPage(nextPage);
      setHasMore(newPokemons.length === 20);
      
      // Aplicar filtros aos novos Pokémon carregados
      applyFiltersToPokemons([...pokemons, ...newPokemons], filters);
    } catch (err) {
      console.error('Erro ao carregar mais Pokémon:', err);
      setError('Falha ao carregar mais Pokémon. Por favor, tente novamente mais tarde.');
    } finally {
      setLoadingMore(false);
    }
  }, [pokemons, page, loadingMore, hasMore, isSearching, filters]);

  // Função para aplicar filtros aos Pokémon
  const applyFiltersToPokemons = (pokemonList, currentFilters) => {
    let filtered = [...pokemonList];
    
    // Filtrar por termo de pesquisa
    if (searchTerm) {
      const term = searchTerm.toLowerCase();
      filtered = filtered.filter(pokemon => 
        pokemon.name.toLowerCase().includes(term) || 
        pokemon.id.toString().includes(term)
      );
    }
    
    // Filtrar por número
    if (currentFilters.minNumber || currentFilters.maxNumber) {
      filtered = filtered.filter(pokemon => 
        pokemon.id >= currentFilters.minNumber && 
        pokemon.id <= currentFilters.maxNumber
      );
    }
    
    // Filtrar por tipos
    if (currentFilters.types && currentFilters.types.length > 0) {
      filtered = filtered.filter(pokemon => {
        if (!pokemon.types) return false;
        return currentFilters.types.some(type => 
          pokemon.types.some(pokemonType => 
            pokemonType && typeof pokemonType === 'string' && pokemonType.toLowerCase() === type.toLowerCase()
          )
        );
      });
    }
    
    // Filtrar por fraquezas
    if (currentFilters.weaknesses && currentFilters.weaknesses.length > 0) {
      filtered = filtered.filter(pokemon => {
        if (!pokemon.weaknesses) return false;
        return currentFilters.weaknesses.some(weakness => 
          pokemon.weaknesses.some(pokemonWeakness => 
            pokemonWeakness && typeof pokemonWeakness === 'string' && pokemonWeakness.toLowerCase() === weakness.toLowerCase()
          )
        );
      });
    }
    
    // Filtrar por habilidades
    if (currentFilters.abilities && currentFilters.abilities.length > 0) {
      filtered = filtered.filter(pokemon => {
        if (!pokemon.abilities) return false;
        return currentFilters.abilities.some(ability => 
          pokemon.abilities.some(pokemonAbility => 
            pokemonAbility && typeof pokemonAbility === 'string' && pokemonAbility.toLowerCase() === ability.toLowerCase()
          )
        );
      });
    }
    
    // Filtrar por altura
    if (currentFilters.heightRange) {
      const [minHeight, maxHeight] = currentFilters.heightRange;
      filtered = filtered.filter(pokemon => {
        const heightInM = pokemon.height / 10; // Convertendo de dm para m
        return heightInM >= minHeight && heightInM <= maxHeight;
      });
    }
    
    // Filtrar por peso
    if (currentFilters.weightRange) {
      const [minWeight, maxWeight] = currentFilters.weightRange;
      filtered = filtered.filter(pokemon => {
        const weightInKg = pokemon.weight / 10; // Convertendo de hg para kg
        return weightInKg >= minWeight && weightInKg <= maxWeight;
      });
    }
    
    // Ordenar os resultados
    if (currentFilters.sortOrder) {
      switch (currentFilters.sortOrder) {
        case 'asc':
          filtered.sort((a, b) => a.id - b.id);
          break;
        case 'desc':
          filtered.sort((a, b) => b.id - a.id);
          break;
        case 'name_asc':
          filtered.sort((a, b) => a.name.localeCompare(b.name));
          break;
        case 'name_desc':
          filtered.sort((a, b) => b.name.localeCompare(a.name));
          break;
        default:
          filtered.sort((a, b) => a.id - b.id);
      }
    }
    
    setFilteredPokemons(filtered);
  };

  // Função para filtrar Pokémon
  const filterPokemons = useCallback((newFilters) => {
    const updatedFilters = { ...filters, ...newFilters };
    setFilters(updatedFilters);
    applyFiltersToPokemons(pokemons, updatedFilters);
  }, [pokemons, filters]);

  // Função para resetar filtros
  const resetFilters = useCallback(() => {
    const defaultFilters = {
      types: [],
      weaknesses: [],
      abilities: [],
      heightRange: [0, 20],
      weightRange: [0, 1000],
      sortOrder: 'asc',
      minNumber: 1,
      maxNumber: 1010
    };
    
    setFilters(defaultFilters);
    applyFiltersToPokemons(pokemons, defaultFilters);
  }, [pokemons]);

  // Função para pesquisar Pokémon
  const searchPokemons = useCallback(async (term) => {
    setSearchTerm(term);
    
    if (!term.trim()) {
      setIsSearching(false);
      applyFiltersToPokemons(pokemons, filters);
      return;
    }
    
    setIsSearching(true);
    
    try {
      // Se o termo for um número, tenta buscar por ID
      if (/^\d+$/.test(term) && parseInt(term) > 0) {
        const id = parseInt(term);
        try {
          const pokemon = await fetchPokemonById(id);
          if (pokemon) {
            setFilteredPokemons([pokemon]);
            return;
          }
        } catch (err) {
          // Se não encontrar por ID, continua com a busca normal
          console.log('Pokémon não encontrado por ID, continuando busca por nome');
        }
      }
      
      // Busca por nome nos Pokémon já carregados
      applyFiltersToPokemons(pokemons, filters);
    } catch (err) {
      console.error('Erro ao pesquisar Pokémon:', err);
      setError('Falha ao pesquisar Pokémon. Por favor, tente novamente mais tarde.');
    }
  }, [pokemons, filters]);

  // Carregar Pokémon iniciais ao montar o componente
  useEffect(() => {
    loadInitialPokemons();
  }, [loadInitialPokemons]);

  // Valores e funções expostos pelo contexto
  const value = {
    pokemons,
    filteredPokemons,
    loading,
    loadingMore,
    error,
    hasMore,
    searchTerm,
    isSearching,
    filters,
    loadMorePokemons,
    filterPokemons,
    resetFilters,
    searchPokemons
  };

  return (
    <PokemonContext.Provider value={value}>
      {children}
    </PokemonContext.Provider>
  );
};

export default PokemonContext; 