import axios from 'axios';
import { POKEMON_LIMIT } from '../config/constants';
import { Pokemon } from '../models/Pokemon';

const API_URL = 'https://pokeapi.co/api/v2';
const LIMIT = 20;

/**
 * Busca uma lista de Pokémon com paginação
 * @param {number} page - Número da página
 * @returns {Promise<Array>} - Lista de Pokémon
 */
export const fetchPokemons = async (page = 1) => {
  try {
    const offset = (page - 1) * LIMIT;
    const response = await axios.get(`${API_URL}/pokemon`, {
      params: {
        limit: LIMIT,
        offset
      }
    });
    
    const results = response.data.results;
    
    // Buscar detalhes de cada Pokémon
    const pokemonDetails = await Promise.all(
      results.map(async (pokemon) => {
        const detailResponse = await axios.get(pokemon.url);
        const data = detailResponse.data;
        
        return {
          id: data.id,
          name: data.name.split('-')[0],
          image: data.sprites.other['official-artwork'].front_default || data.sprites.front_default,
          sprites: {
            default: data.sprites.front_default,
            animated: data.sprites.versions['generation-v']['black-white'].animated.front_default 
              ? {
                  front_default: data.sprites.versions['generation-v']['black-white'].animated.front_default,
                  back_default: data.sprites.versions['generation-v']['black-white'].animated.back_default
                }
              : null
          },
          types: data.types.map(type => type.type.name),
          height: data.height,
          weight: data.weight,
          abilities: data.abilities.map(ability => ability.ability.name),
          stats: data.stats.reduce((acc, stat) => {
            const statName = stat.stat.name
              .replace(/-/g, ' ')
              .split(' ')
              .map(word => word.charAt(0).toUpperCase() + word.slice(1))
              .join('');
            
            acc[statName] = stat.base_stat;
            return acc;
          }, {})
        };
      })
    );
    
    return pokemonDetails;
  } catch (error) {
    console.error('Erro ao buscar Pokémon:', error);
    throw new Error('Falha ao buscar Pokémon');
  }
};

/**
 * Busca um Pokémon pelo ID
 * @param {number} id - ID do Pokémon
 * @returns {Promise<Object>} - Detalhes do Pokémon
 */
export const fetchPokemonById = async (id) => {
  try {
    const response = await axios.get(`${API_URL}/pokemon/${id}`);
    const data = response.data;
    
    // Buscar descrição do Pokémon
    let description = '';
    try {
      const speciesResponse = await axios.get(data.species.url);
      const flavorTextEntries = speciesResponse.data.flavor_text_entries;
      const portugueseEntry = flavorTextEntries.find(entry => entry.language.name === 'pt-br');
      const englishEntry = flavorTextEntries.find(entry => entry.language.name === 'en');
      
      description = (portugueseEntry || englishEntry)?.flavor_text.replace(/\f/g, ' ') || '';
    } catch (err) {
      console.warn('Não foi possível buscar a descrição do Pokémon:', err);
    }
    
    return {
      id: data.id,
      name: data.name.split('-')[0],
      image: data.sprites.other['official-artwork'].front_default || data.sprites.front_default,
      sprites: {
        default: data.sprites.front_default,
        animated: data.sprites.versions['generation-v']['black-white'].animated.front_default 
          ? {
              front_default: data.sprites.versions['generation-v']['black-white'].animated.front_default,
              back_default: data.sprites.versions['generation-v']['black-white'].animated.back_default
            }
          : null
      },
      types: data.types.map(type => type.type.name),
      height: data.height,
      weight: data.weight,
      abilities: data.abilities.map(ability => ability.ability.name),
      stats: data.stats.reduce((acc, stat) => {
        const statName = stat.stat.name
          .replace(/-/g, ' ')
          .split(' ')
          .map(word => word.charAt(0).toUpperCase() + word.slice(1))
          .join('');
        
        acc[statName] = stat.base_stat;
        return acc;
      }, {}),
      description
    };
  } catch (error) {
    console.error('Erro ao buscar Pokémon por ID:', error);
    throw new Error('Falha ao buscar Pokémon por ID');
  }
};

/**
 * Busca Pokémons pelo nome
 * @param {string} name - Nome ou parte do nome do Pokémon
 * @returns {Promise<Array>} - Lista de Pokémons que correspondem à busca
 */
export const searchPokemons = async (name) => {
  try {
    // Como a API não suporta busca por nome parcial, vamos buscar todos e filtrar
    const allPokemons = await fetchPokemons(200); // Buscamos um número maior para ter mais resultados na busca
    return allPokemons.filter(pokemon => 
      pokemon.name.toLowerCase().includes(name.toLowerCase())
    );
  } catch (error) {
    console.error('Erro ao buscar Pokémons por nome:', error);
    throw new Error('Não foi possível realizar a busca. Tente novamente mais tarde.');
  }
};

/**
 * Extrai a cadeia de evolução a partir dos dados da API
 * @param {Object} chain - Objeto da cadeia de evolução da API
 * @returns {Array} - Array com a cadeia de evolução formatada
 */
const extractEvolutionChain = (chain) => {
  const evolutions = [];
  
  // Adiciona o primeiro Pokémon da cadeia
  const addEvolution = (pokemon, level = null, trigger = null, item = null) => {
    const id = extractIdFromUrl(pokemon.species.url);
    evolutions.push({
      id,
      name: pokemon.species.name,
      level,
      trigger,
      item,
    });
  };
  
  // Função recursiva para percorrer a cadeia de evolução
  const processChain = (currentChain, level = null, trigger = null, item = null) => {
    addEvolution(currentChain, level, trigger, item);
    
    if (currentChain.evolves_to && currentChain.evolves_to.length > 0) {
      currentChain.evolves_to.forEach(evolution => {
        const evolutionDetails = evolution.evolution_details[0] || {};
        const newLevel = evolutionDetails.min_level || null;
        const newTrigger = evolutionDetails.trigger?.name || null;
        const newItem = evolutionDetails.item?.name || null;
        
        processChain(evolution, newLevel, newTrigger, newItem);
      });
    }
  };
  
  processChain(chain);
  return evolutions;
};

/**
 * Extrai o ID do Pokémon a partir da URL
 * @param {string} url - URL da API
 * @returns {number} - ID do Pokémon
 */
const extractIdFromUrl = (url) => {
  const matches = url.match(/\/(\d+)\//);
  return matches ? parseInt(matches[1], 10) : null;
}; 