import React, { useState, useEffect } from 'react';
import { 
  AppBar, 
  Toolbar, 
  InputBase, 
  IconButton, 
  Box, 
  useTheme,
  alpha,
  Typography
} from '@mui/material';
import SearchIcon from '@mui/icons-material/Search';
import CloseIcon from '@mui/icons-material/Close';
import { usePokemon } from '../../context/PokemonContext';

/**
 * Barra de pesquisa
 * @returns {JSX.Element} - Elemento JSX
 */
const SearchBar = () => {
  const [searchValue, setSearchValue] = useState('');
  const [debouncedValue, setDebouncedValue] = useState('');
  const { searchPokemons, searchTerm } = usePokemon();
  const theme = useTheme();
  
  // Efeito para debounce da pesquisa
  useEffect(() => {
    const timer = setTimeout(() => {
      setDebouncedValue(searchValue);
    }, 500);
    
    return () => clearTimeout(timer);
  }, [searchValue]);
  
  // Efeito para realizar a pesquisa
  useEffect(() => {
    if (debouncedValue !== searchTerm) {
      searchPokemons(debouncedValue);
    }
  }, [debouncedValue, searchPokemons, searchTerm]);
  
  // Limpar a pesquisa
  const handleClearSearch = () => {
    setSearchValue('');
    searchPokemons('');
  };
  
  return (
    <AppBar 
      position="static" 
      color="transparent" 
      elevation={0}
      sx={{ 
        borderBottom: `1px solid ${theme.palette.divider}`,
        backgroundColor: theme.palette.background.paper
      }}
    >
      <Toolbar>
        <Box 
          display="flex" 
          alignItems="center"
          width="100%"
          justifyContent="space-between"
        >
          <Typography 
            variant="h6" 
            component="div"
            sx={{ 
              display: 'flex', 
              alignItems: 'center',
              color: theme.palette.primary.main,
              fontWeight: 'bold'
            }}
          >
            <Box 
              component="img"
              src="/pokeball.png"
              alt="Pokeball"
              sx={{ 
                width: 30, 
                height: 30,
                mr: 1
              }}
            />
            Pokédex
          </Typography>
          
          <Box 
            sx={{ 
              position: 'relative',
              borderRadius: theme.shape.borderRadius,
              backgroundColor: alpha(theme.palette.common.black, 0.05),
              '&:hover': {
                backgroundColor: alpha(theme.palette.common.black, 0.1),
              },
              width: { xs: '100%', sm: 'auto' },
              ml: { xs: 0, sm: 2 },
              maxWidth: { xs: '100%', sm: 400 }
            }}
          >
            <Box sx={{ 
              padding: theme.spacing(0, 2),
              height: '100%',
              position: 'absolute',
              pointerEvents: 'none',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
            }}>
              <SearchIcon />
            </Box>
            <InputBase
              placeholder="Buscar Pokémon..."
              value={searchValue}
              onChange={(e) => setSearchValue(e.target.value)}
              sx={{
                color: 'inherit',
                width: '100%',
                '& .MuiInputBase-input': {
                  padding: theme.spacing(1, 1, 1, 0),
                  paddingLeft: `calc(1em + ${theme.spacing(4)})`,
                  transition: theme.transitions.create('width'),
                  width: '100%',
                },
              }}
            />
            {searchValue && (
              <IconButton
                size="small"
                aria-label="clear"
                onClick={handleClearSearch}
                sx={{
                  position: 'absolute',
                  right: 8,
                  top: '50%',
                  transform: 'translateY(-50%)',
                }}
              >
                <CloseIcon fontSize="small" />
              </IconButton>
            )}
          </Box>
        </Box>
      </Toolbar>
    </AppBar>
  );
};

export default SearchBar; 