import React from 'react';
import { 
  Card, 
  CardContent, 
  CardMedia, 
  Typography, 
  Box, 
  useTheme 
} from '@mui/material';

/**
 * Componente de card de Pokémon
 * @param {Object} props - Propriedades do componente
 * @param {Object} props.pokemon - Dados do Pokémon
 * @param {Function} props.onClick - Função a ser chamada ao clicar no card
 * @param {boolean} props.isSelected - Indica se o card está selecionado
 * @returns {JSX.Element} - Elemento JSX
 */
const PokemonCard = ({ pokemon, onClick, isSelected }) => {
  const theme = useTheme();
  
  if (!pokemon) return null;
  
  return (
    <Card 
      sx={{ 
        height: '100%', 
        display: 'flex', 
        flexDirection: 'column',
        cursor: 'pointer',
        border: isSelected ? `2px solid ${theme.palette.primary.main}` : 'none',
        transform: isSelected ? 'scale(1.03)' : 'scale(1)',
        transition: 'transform 0.2s, border 0.2s',
        '&:hover': {
          transform: isSelected ? 'scale(1.03)' : 'scale(1.05)',
        }
      }}
      onClick={() => onClick(pokemon)}
    >
      <Box 
        sx={{ 
          backgroundColor: '#F8F9FA',
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          pt: 2,
          position: 'relative'
        }}
      >
        <Typography 
          variant="caption" 
          sx={{ 
            position: 'absolute',
            top: 8,
            right: 8,
            backgroundColor: 'rgba(0, 0, 0, 0.6)',
            color: 'white',
            px: 1,
            py: 0.5,
            borderRadius: 1,
            fontWeight: 'bold'
          }}
        >
          #{pokemon.id.toString().padStart(3, '0')}
        </Typography>
        
        <CardMedia
          component="img"
          image={pokemon.sprites?.animated?.front_default || pokemon.image}
          alt={pokemon.name}
          sx={{ 
            width: 120, 
            height: 120, 
            objectFit: 'contain',
            transition: 'transform 0.3s ease-in-out',
            '&:hover': {
              transform: 'scale(1.1)',
            }
          }}
        />
      </Box>
      
      <CardContent sx={{ flexGrow: 1, pt: 1 }}>
        <Typography 
          variant="h6" 
          component="div" 
          align="center"
          sx={{ 
            textTransform: 'capitalize',
            fontWeight: 'bold',
            fontSize: '1rem',
            mb: 1
          }}
        >
          {pokemon.name}
        </Typography>
        
        <Box 
          sx={{ 
            display: 'flex', 
            justifyContent: 'center', 
            gap: 0.5,
            flexWrap: 'wrap'
          }}
        >
          {pokemon.types && pokemon.types.map((type) => (
            <Box
              key={type}
              sx={{
                backgroundColor: theme.palette.pokemonTypes[type.toLowerCase()] || theme.palette.grey[500],
                color: 'white',
                px: 1,
                py: 0.2,
                borderRadius: 1,
                fontSize: '0.7rem',
                fontWeight: 'bold',
                textTransform: 'capitalize'
              }}
            >
              {type}
            </Box>
          ))}
        </Box>
      </CardContent>
    </Card>
  );
};

export default PokemonCard; 