import React, { useState, useEffect } from 'react';
import { 
  Box, 
  Button, 
  Container, 
  Popover, 
  FormGroup, 
  FormControlLabel, 
  Checkbox,
  Typography,
  Slider,
  Divider,
  useTheme,
  alpha,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  TextField,
  Chip,
  IconButton,
  Paper,
  Grid,
  Drawer,
  useMediaQuery
} from '@mui/material';
import { usePokemon } from '../../context/PokemonContext';
import FilterListIcon from '@mui/icons-material/FilterList';
import CloseIcon from '@mui/icons-material/Close';

// Lista de tipos de Pokémon
const pokemonTypesList = [
  'normal', 'fire', 'water', 'electric', 'grass', 'ice', 
  'fighting', 'poison', 'ground', 'flying', 'psychic', 
  'bug', 'rock', 'ghost', 'dragon', 'dark', 'steel', 'fairy'
];

// Lista de habilidades comuns
const commonAbilities = [
  'overgrow', 'blaze', 'torrent', 'shield-dust', 'shed-skin', 
  'compound-eyes', 'swarm', 'keen-eye', 'run-away', 'intimidate', 
  'static', 'sand-veil', 'lightning-rod', 'chlorophyll', 'swift-swim'
];

/**
 * Componente de filtro para Pokémon
 * @returns {JSX.Element} - Elemento JSX
 */
const PokemonFilter = () => {
  const theme = useTheme();
  const { filterPokemons, resetFilters, filters } = usePokemon();
  
  const [drawerOpen, setDrawerOpen] = useState(false);
  const [selectedTypes, setSelectedTypes] = useState(filters.types || []);
  const [selectedWeaknesses, setSelectedWeaknesses] = useState(filters.weaknesses || []);
  const [selectedAbilities, setSelectedAbilities] = useState(filters.abilities || []);
  const [heightRange, setHeightRange] = useState([0, 20]); // em metros
  const [weightRange, setWeightRange] = useState([0, 1000]); // em kg
  const [sortOrder, setSortOrder] = useState(filters.sortOrder || 'asc');
  const [minNumber, setMinNumber] = useState(filters.minNumber || 1);
  const [maxNumber, setMaxNumber] = useState(filters.maxNumber || 1010);
  
  const isMobile = useMediaQuery(theme.breakpoints.down('md'));
  
  // Funções para manipular mudanças nos filtros
  const handleTypeChange = (type) => {
    const newSelectedTypes = selectedTypes.includes(type)
      ? selectedTypes.filter(t => t !== type)
      : [...selectedTypes, type];
    
    setSelectedTypes(newSelectedTypes);
  };
  
  const handleWeaknessChange = (weakness) => {
    const newSelectedWeaknesses = selectedWeaknesses.includes(weakness)
      ? selectedWeaknesses.filter(w => w !== weakness)
      : [...selectedWeaknesses, weakness];
    
    setSelectedWeaknesses(newSelectedWeaknesses);
  };
  
  const handleAbilityChange = (ability) => {
    const newSelectedAbilities = selectedAbilities.includes(ability)
      ? selectedAbilities.filter(a => a !== ability)
      : [...selectedAbilities, ability];
    
    setSelectedAbilities(newSelectedAbilities);
  };
  
  const handleHeightChange = (event, newValue) => {
    setHeightRange(newValue);
  };
  
  const handleWeightChange = (event, newValue) => {
    setWeightRange(newValue);
  };
  
  const handleSortOrderChange = (e) => {
    setSortOrder(e.target.value);
  };
  
  const handleMinNumberChange = () => {
    // Implementar lógica para mudar o número mínimo
  };
  
  const handleMaxNumberChange = () => {
    // Implementar lógica para mudar o número máximo
  };
  
  // Aplicar filtros
  const applyFilters = () => {
    try {
      filterPokemons({
        types: selectedTypes,
        weaknesses: selectedWeaknesses,
        abilities: selectedAbilities,
        sortOrder,
        minNumber,
        maxNumber
      });
      
      if (isMobile) {
        setDrawerOpen(false);
      }
    } catch (error) {
      console.error('Erro ao aplicar filtros:', error);
    }
  };
  
  // Limpar filtros
  const handleResetFilters = () => {
    setSelectedTypes([]);
    setSelectedWeaknesses([]);
    setSelectedAbilities([]);
    setHeightRange([0, 20]);
    setWeightRange([0, 1000]);
    setSortOrder('asc');
    setMinNumber(1);
    setMaxNumber(1010);
    resetFilters();
    
    if (isMobile) {
      setDrawerOpen(false);
    }
  };
  
  // Alternar tipo
  const toggleType = (type) => {
    if (selectedTypes.includes(type)) {
      setSelectedTypes(selectedTypes.filter(t => t !== type));
    } else {
      setSelectedTypes([...selectedTypes, type]);
    }
  };
  
  // Alternar fraqueza
  const toggleWeakness = (weakness) => {
    if (selectedWeaknesses.includes(weakness)) {
      setSelectedWeaknesses(selectedWeaknesses.filter(w => w !== weakness));
    } else {
      setSelectedWeaknesses([...selectedWeaknesses, weakness]);
    }
  };
  
  // Atualizar filtros quando os valores mudarem
  useEffect(() => {
    // Não aplicar automaticamente para evitar muitas chamadas
  }, [selectedTypes, selectedWeaknesses, selectedAbilities, sortOrder, minNumber, maxNumber]);
  
  // Conteúdo do filtro
  const filterContent = (
    <Box p={3}>
      <Box display="flex" justifyContent="space-between" alignItems="center" mb={3}>
        <Typography variant="h5">Filtros</Typography>
        {isMobile && (
          <IconButton onClick={() => setDrawerOpen(false)}>
            <CloseIcon />
          </IconButton>
        )}
      </Box>
      
      <Grid container spacing={3}>
        {/* Ordenação */}
        <Grid item xs={12} md={6}>
          <FormControl fullWidth>
            <InputLabel id="sort-order-label">Ordenar por</InputLabel>
            <Select
              labelId="sort-order-label"
              id="sort-order"
              value={sortOrder}
              label="Ordenar por"
              onChange={handleSortOrderChange}
            >
              <MenuItem value="asc">Número (Crescente)</MenuItem>
              <MenuItem value="desc">Número (Decrescente)</MenuItem>
              <MenuItem value="name_asc">Nome (A-Z)</MenuItem>
              <MenuItem value="name_desc">Nome (Z-A)</MenuItem>
            </Select>
          </FormControl>
        </Grid>
        
        {/* Intervalo de números */}
        <Grid item xs={12} md={6}>
          <Typography gutterBottom>Intervalo de Números</Typography>
          <Box display="flex" gap={2}>
            <TextField
              label="Min"
              type="number"
              value={minNumber}
              onChange={(e) => setMinNumber(Number(e.target.value))}
              inputProps={{ min: 1, max: 1010 }}
              size="small"
            />
            <TextField
              label="Max"
              type="number"
              value={maxNumber}
              onChange={(e) => setMaxNumber(Number(e.target.value))}
              inputProps={{ min: 1, max: 1010 }}
              size="small"
            />
          </Box>
        </Grid>
        
        {/* Tipos */}
        <Grid item xs={12}>
          <Typography gutterBottom>Tipos</Typography>
          <Box display="flex" flexWrap="wrap" gap={1}>
            {pokemonTypesList.map(type => (
              <Chip
                key={type}
                label={type}
                onClick={() => toggleType(type)}
                sx={{
                  bgcolor: selectedTypes.includes(type) 
                    ? theme.palette.pokemonTypes[type] 
                    : 'transparent',
                  color: selectedTypes.includes(type) 
                    ? 'white' 
                    : 'text.primary',
                  border: `1px solid ${theme.palette.pokemonTypes[type]}`,
                  textTransform: 'capitalize'
                }}
              />
            ))}
          </Box>
        </Grid>
        
        {/* Fraquezas */}
        <Grid item xs={12}>
          <Typography gutterBottom>Fraquezas</Typography>
          <Box display="flex" flexWrap="wrap" gap={1}>
            {pokemonTypesList.map(weakness => (
              <Chip
                key={weakness}
                label={weakness}
                onClick={() => toggleWeakness(weakness)}
                sx={{
                  bgcolor: selectedWeaknesses.includes(weakness) 
                    ? theme.palette.pokemonTypes[weakness] 
                    : 'transparent',
                  color: selectedWeaknesses.includes(weakness) 
                    ? 'white' 
                    : 'text.primary',
                  border: `1px solid ${theme.palette.pokemonTypes[weakness]}`,
                  textTransform: 'capitalize'
                }}
              />
            ))}
          </Box>
        </Grid>
        
        {/* Botões de ação */}
        <Grid item xs={12}>
          <Box display="flex" gap={2} mt={2}>
            <Button 
              variant="contained" 
              color="primary" 
              onClick={applyFilters}
              fullWidth
            >
              Aplicar Filtros
            </Button>
            <Button 
              variant="outlined" 
              onClick={handleResetFilters}
              fullWidth
            >
              Limpar Filtros
            </Button>
          </Box>
        </Grid>
      </Grid>
    </Box>
  );
  
  return (
    <>
      {/* Botão de filtro para mobile */}
      {isMobile && (
        <Box 
          position="fixed" 
          bottom={20} 
          right={20} 
          zIndex={1000}
          onClick={() => setDrawerOpen(true)}
        >
          <Paper
            elevation={3}
            sx={{
              width: 56,
              height: 56,
              borderRadius: '50%',
              display: 'flex',
              justifyContent: 'center',
              alignItems: 'center',
              bgcolor: theme.palette.primary.main,
              color: 'white',
              cursor: 'pointer'
            }}
          >
            <FilterListIcon />
          </Paper>
        </Box>
      )}
      
      {/* Drawer para mobile */}
      {isMobile ? (
        <Drawer
          anchor="bottom"
          open={drawerOpen}
          onClose={() => setDrawerOpen(false)}
          PaperProps={{
            sx: {
              maxHeight: '80vh',
              borderTopLeftRadius: 16,
              borderTopRightRadius: 16
            }
          }}
        >
          {filterContent}
        </Drawer>
      ) : (
        <Paper 
          elevation={2} 
          sx={{ 
            mb: 3, 
            mx: 3,
            borderRadius: 2
          }}
        >
          {filterContent}
        </Paper>
      )}
    </>
  );
};

export default PokemonFilter; 