import React, { useState, useEffect, useRef, useCallback } from 'react';
import { 
  Container, 
  Grid, 
  Typography, 
  Box, 
  CircularProgress, 
  useMediaQuery, 
  useTheme,
  Paper,
  Divider
} from '@mui/material';
import PokemonCard from '../components/pokemon/PokemonCard';
import LoadingMore from '../components/common/LoadingMore';
import PokemonFilter from '../components/pokemon/PokemonFilter';
import { usePokemon } from '../context/PokemonContext';
import { fetchPokemonById } from '../services/pokemonService';

/**
 * Página inicial
 * @returns {JSX.Element} - Elemento JSX
 */
const Home = () => {
  const { 
    filteredPokemons, 
    loading, 
    loadingMore, 
    error, 
    hasMore, 
    loadMorePokemons, 
    searchTerm,
    isSearching
  } = usePokemon();
  
  const [selectedPokemon, setSelectedPokemon] = useState(null);
  const [loadingPokemonDetails, setLoadingPokemonDetails] = useState(false);
  const [detailsError, setDetailsError] = useState(null);
  
  const observer = useRef();
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));
  
  // Função para lidar com a seleção de um Pokémon
  const handleSelectPokemon = async (pokemon) => {
    if (selectedPokemon && selectedPokemon.id === pokemon.id) {
      // Se o mesmo Pokémon for clicado novamente, desselecionar
      setSelectedPokemon(null);
      return;
    }
    
    try {
      setLoadingPokemonDetails(true);
      setDetailsError(null);
      
      // Buscar detalhes completos do Pokémon
      const detailedPokemon = await fetchPokemonById(pokemon.id);
      setSelectedPokemon(detailedPokemon);
    } catch (err) {
      console.error('Erro ao carregar detalhes do Pokémon:', err);
      setDetailsError('Não foi possível carregar os detalhes deste Pokémon.');
    } finally {
      setLoadingPokemonDetails(false);
    }
  };
  
  // Efeito para limpar o Pokémon selecionado quando o termo de pesquisa muda
  useEffect(() => {
    setSelectedPokemon(null);
  }, [searchTerm]);
  
  // Configuração do observador para rolagem infinita
  const lastPokemonElementRef = useCallback(node => {
    if (loading || loadingMore) return;
    if (observer.current) observer.current.disconnect();
    
    observer.current = new IntersectionObserver(entries => {
      if (entries[0].isIntersecting && hasMore && !isSearching) {
        loadMorePokemons();
      }
    });
    
    if (node) observer.current.observe(node);
  }, [loading, loadingMore, hasMore, loadMorePokemons, isSearching]);
  
  // Renderização condicional para diferentes estados
  if (loading && !loadingMore) {
    return (
      <Box display="flex" justifyContent="center" alignItems="center" minHeight="80vh">
        <CircularProgress size={60} />
      </Box>
    );
  }
  
  if (error) {
    return (
      <Container maxWidth="xl">
        <Box textAlign="center" my={5}>
          <Typography variant="h5" color="error" gutterBottom>
            {error}
          </Typography>
        </Box>
      </Container>
    );
  }
  
  return (
    <>
      <PokemonFilter />
      
      <Container maxWidth="xl">
        <Grid container spacing={3}>
          {/* Lista de Pokémon */}
          <Grid item xs={12} md={selectedPokemon ? 8 : 12}>
            {filteredPokemons.length === 0 ? (
              <Box textAlign="center" my={5}>
                <Typography variant="h5" gutterBottom>
                  Nenhum Pokémon encontrado
                </Typography>
                <Typography variant="body1">
                  Tente ajustar seus filtros ou termos de pesquisa.
                </Typography>
              </Box>
            ) : (
              <>
                <Grid container spacing={2}>
                  {filteredPokemons.map((pokemon, index) => {
                    const isLastElement = index === filteredPokemons.length - 1;
                    
                    return (
                      <Grid 
                        item 
                        xs={6} 
                        sm={4} 
                        md={selectedPokemon ? 4 : 3} 
                        lg={selectedPokemon ? 3 : 2} 
                        key={pokemon.id}
                        ref={isLastElement ? lastPokemonElementRef : null}
                      >
                        <PokemonCard 
                          pokemon={pokemon} 
                          onClick={handleSelectPokemon}
                          isSelected={selectedPokemon && selectedPokemon.id === pokemon.id}
                        />
                      </Grid>
                    );
                  })}
                </Grid>
                
                {loadingMore && <LoadingMore />}
                
                {!hasMore && !isSearching && (
                  <Box textAlign="center" my={3}>
                    <Typography variant="body1">
                      Você chegou ao fim da lista de Pokémon!
                    </Typography>
                  </Box>
                )}
              </>
            )}
          </Grid>
          
          {/* Detalhes do Pokémon selecionado */}
          {selectedPokemon && !isMobile && (
            <Grid item xs={12} md={4}>
              <Paper 
                elevation={3} 
                sx={{ 
                  p: 3, 
                  position: 'sticky', 
                  top: 20, 
                  maxHeight: 'calc(100vh - 40px)',
                  overflowY: 'auto'
                }}
              >
                {loadingPokemonDetails ? (
                  <Box display="flex" justifyContent="center" alignItems="center" minHeight="300px">
                    <CircularProgress />
                  </Box>
                ) : detailsError ? (
                  <Box textAlign="center" my={3}>
                    <Typography variant="body1" color="error">
                      {detailsError}
                    </Typography>
                  </Box>
                ) : (
                  <>
                    <Box 
                      display="flex" 
                      flexDirection="column" 
                      alignItems="center"
                      mb={3}
                    >
                      <Typography variant="h4" gutterBottom>
                        {selectedPokemon.name}
                      </Typography>
                      <Typography variant="subtitle1" color="text.secondary" gutterBottom>
                        #{selectedPokemon.id.toString().padStart(3, '0')}
                      </Typography>
                      
                      <Box 
                        component="img"
                        src={selectedPokemon.sprites?.animated?.front_default || selectedPokemon.image}
                        alt={selectedPokemon.name}
                        sx={{ 
                          width: '180px', 
                          height: '180px',
                          objectFit: 'contain',
                          mt: 2
                        }}
                      />
                      
                      <Box display="flex" gap={1} mt={2}>
                        {selectedPokemon.types.map(type => (
                          <Box
                            key={type}
                            sx={{
                              bgcolor: theme.palette.pokemonTypes[type.toLowerCase()] || theme.palette.grey[500],
                              color: 'white',
                              px: 2,
                              py: 0.5,
                              borderRadius: 2,
                              textTransform: 'capitalize',
                              fontWeight: 'bold',
                              fontSize: '0.875rem'
                            }}
                          >
                            {type}
                          </Box>
                        ))}
                      </Box>
                    </Box>
                    
                    <Divider sx={{ my: 2 }} />
                    
                    {selectedPokemon.description && (
                      <Box mb={3}>
                        <Typography variant="h6" gutterBottom>
                          Descrição
                        </Typography>
                        <Typography variant="body2">
                          {selectedPokemon.description}
                        </Typography>
                      </Box>
                    )}
                    
                    <Grid container spacing={2} mb={3}>
                      <Grid item xs={6}>
                        <Typography variant="subtitle2" color="text.secondary">
                          Altura
                        </Typography>
                        <Typography variant="body1">
                          {(selectedPokemon.height / 10).toFixed(1)}m
                        </Typography>
                      </Grid>
                      <Grid item xs={6}>
                        <Typography variant="subtitle2" color="text.secondary">
                          Peso
                        </Typography>
                        <Typography variant="body1">
                          {(selectedPokemon.weight / 10).toFixed(1)}kg
                        </Typography>
                      </Grid>
                    </Grid>
                    
                    <Box mb={3}>
                      <Typography variant="h6" gutterBottom>
                        Estatísticas
                      </Typography>
                      <Grid container spacing={1}>
                        {Object.entries(selectedPokemon.stats).map(([statName, baseStat]) => (
                          <Grid item xs={6} key={statName}>
                            <Typography variant="subtitle2" color="text.secondary">
                              {statName.replace(/([A-Z])/g, ' $1').trim()}
                            </Typography>
                            <Box display="flex" alignItems="center">
                              <Box
                                sx={{
                                  width: `${Math.min(100, (baseStat / 255) * 100)}%`,
                                  height: 8,
                                  bgcolor: theme.palette.primary.main,
                                  borderRadius: 1,
                                  mr: 1
                                }}
                              />
                              <Typography variant="body2">
                                {baseStat}
                              </Typography>
                            </Box>
                          </Grid>
                        ))}
                      </Grid>
                    </Box>
                  </>
                )}
              </Paper>
            </Grid>
          )}
        </Grid>
      </Container>
    </>
  );
};

export default Home;
